import React, { FC } from 'react';
import { UserStatus } from '../../App';
import AboutMe from '../AboutMe/AboutMe.lazy';
import Info from '../Info/Info.lazy';
import MyArticles from '../MyArticles/MyArticles.lazy';
import PwaInstall from '../PWAInstall/PWAInstall.lazy';
import QuickNav from '../QuickNav/QuickNav.lazy';
import Reminder from '../Reminder/Reminder.lazy';
import MyActivity from '../MyActivity/MyActivity.lazy';
import Tools from '../Tools/Tools.lazy';
import UserStatusButton from '../UserStatusButton/UserStatusButton.lazy';
import WorkedAndLearned from '../WorkedAndLearned/WorkedAndLearned.lazy';
import './Menu.scss';
import MyWork from '../MyWork/MyWork';
import MyPackages from '../MyPackages/MyPackages';
import MyCertificates from '../MyCertificates/MyCertificates';
//import PWAInstallerPrompt from '../PWAInstallerPrompt/PWAInstallerPrompt';

interface MenuProps {}

const Menu: FC<MenuProps> = () =>{  
  return(   
    <div id="app-menu">      
      <div id="app-menu-content-wrapper">
        <div id="app-menu-content">
          <div id="app-menu-content-header">
            <div className="app-menu-content-header-section">
              <Info id="app-menu-info" />
              <Reminder />
            </div>
            <div className="app-menu-content-header-section">
              <UserStatusButton 
                icon="right-from-bracket" 
                 id="sign-out-button" 
                userStatus={UserStatus.LoggedOut}
              />
              
            </div>
          </div>
          <QuickNav />
          {/* <PwaInstall/> */}
         
          {/* <a id="youtube-link" className="clear-button" href="https://www.youtube.com/c/Hyperplexed" target="_blank">
            <i className="fa-brands fa-youtube" />  
            <span>Hyperplexed</span>
          </a> */}
          <AboutMe />
          <WorkedAndLearned />  
          <MyCertificates />                
          <MyActivity />
          <MyWork />
          <MyPackages />
          <Tools />
          <MyArticles />
        </div>
      </div>
    </div>
  )
}

export default Menu;
