import React, { FC, useEffect, useState } from 'react';
import MenuSection from '../MenuSection/MenuSection.lazy';
import './MyWork.scss';



interface MyWorkProps {}

interface GithubInterface{
  timeline: string,
  title: string,
  uri: string,
  tags: string[]
}


const redirectToLink=(url:string):any =>{
  const win = window.open("https://github.com"+url, '_blank');
  if (win != null) {
    win.focus();
  }
  return win;
}
function loaderGithubData(): JSX.Element{
  return  <div className="loading-dim">
    <div className="loader"></div>
    <div className="loader"></div>
    <div className="loader"></div>
  </div>  
}

function displayGithubData(githubInterfaceData: GithubInterface[]): JSX.Element[]{
 let id=0;
 let image= "/assets/brand-logo/GitHub-Logo.svg"
  return  githubInterfaceData.map((githubRepoList: any) => {
    githubRepoList.id=id+=1;
    const styles: React.CSSProperties = {
      backgroundImage: `url(${image})`  ,
      backgroundSize: `contain` ,
      backgroundPositionX: `left` 
    }
    
    return (
      <div
        key={githubRepoList.id}
        className="stackOverFlow-card background-image"
        style={styles}
        onClick={(event: React.MouseEvent<HTMLElement>) => {redirectToLink(githubRepoList.uri)}}
      >
        <div className="github-card-content">
          <div className="stackOverFlow-card-content-items">
            <span className="stackOverFlow-card-title">
              {"stackQuesList.title".length > 100
                ? githubRepoList.title.substring(0, 100) + "..."
                : githubRepoList.title}
            </span>
            <span className="stackOverFlow-card-desc">
              {githubRepoList.tags}
            </span> 
            <span className="stackOverFlow-card-date">
              Updated on {githubRepoList.timeline}
            </span> 
          </div>
        </div>
      </div>
    );
  });
}


