import React, { FC, useEffect, useState } from 'react';
import MenuSection from '../MenuSection/MenuSection.lazy';
import './MyPackages.scss';



interface MyPackagesProps {}

interface Npm{
  timeline: string,
  title: string,
  uri: string,
  tags: string[]
}


const redirectToLink=(url:string):any =>{
  const win = window.open("https://www.npmjs.com"+url, '_blank');
  if (win != null) {
    win.focus();
  }
  return win;
}

function loaderNpmData(): JSX.Element{
  return  <div className="loading-dim">
    <div className="loader1"></div>
    <div className="loader1"></div>
    <div className="loader1"></div>
  </div>  
}

function displayNpmData(npm: Npm[]): JSX.Element[]{
 let id=0;
 let image= "/assets/brand-logo/Npm-logo.svg"
  return  npm.map((npmList: any) => {
    npmList.id=id+=1;
    const styles: React.CSSProperties = {
      backgroundImage: `url(${image})`  ,
      backgroundSize: `contain` ,
      backgroundPositionX: `left`
    }
    
    return (
      <div
        key={npmList.id}
        className="stackOverFlow-card background-image"
        style={styles}
        onClick={(event: React.MouseEvent<HTMLElement>) => {redirectToLink(npmList.uri)}}
      >
        <div className="stackOverFlow-card-content">
          <div className="stackOverFlow-card-content-items">
            <span className="stackOverFlow-card-title">
              {"stackQuesList.title".length > 100
                ? npmList.title.substring(0, 100) + "..."
                : npmList.title}
            </span>
            <span className="stackOverFlow-card-desc">
              {npmList.tags}
            </span> 
            <span className="stackOverFlow-card-date">
              Updated {npmList.timeline}
            </span> 
          </div>
        </div>
      </div>
    );
  });
}


const MyPackages: FC<MyPackagesProps> = () => {  
  const [npmsList, setNpmsList] = useState([]);
const [status, setStatus] = useState('');
  useEffect(() => {
    findStackOverFlow();
  }, [1]);

  const findStackOverFlow=async () =>{

    let npmflowData= [] as Npm[];
   //{timeline: "", title : "", uri:""};
    fetch("https://stackscrap-1-a3088001.deta.app/npm/pkg", {
     method: "GET",
     headers: { "Content-Type": "application/json" },
   })
     .then((response) => response.json())
     .then((jsonResponse) =>{
      
      npmflowData = jsonResponse.response as Npm[];
  
      npmflowData.length=4;
      setNpmsList(jsonResponse.response)
      console.log(npmflowData)
      }).then(()=>setStatus('Success'))
      .catch((err)=>{setStatus('Error')
        console.error(err)
      });
  }

    return( 
    <MenuSection icon="bowl-food" id="stackOverFlows-section" title="My packages">
      { status === 'Success' ? displayNpmData(npmsList) : loaderNpmData()}
    </MenuSection>
  )
}

export default MyPackages;
