import React, { lazy, Suspense } from 'react';
import {IMenuSectionProps} from './MenuSection';

const LazyMenuSection = lazy(() => import('./MenuSection'));

const MenuSection = (menuSec:IMenuSectionProps,props: JSX.IntrinsicAttributes & { children?: React.ReactNode; }) => (
  <Suspense fallback={null}>
    <LazyMenuSection children={menuSec.children} icon={menuSec.icon} id={menuSec.id} scrollable={menuSec.scrollable} title={menuSec.title} {...props} />
  </Suspense>
);

export default MenuSection;
