import React, { lazy, Suspense } from 'react';

const LazyQuickNav = lazy(() => import('./QuickNav'));

const QuickNav = (props: JSX.IntrinsicAttributes & { children?: React.ReactNode; }) => (
  <Suspense fallback={null}>
    <LazyQuickNav {...props} />
  </Suspense>
);

export default QuickNav;
