import React, { FC, useEffect, useState } from 'react';
import MenuSection from '../MenuSection/MenuSection.lazy';
import './MyCertificates.scss';



interface MyCertificatesProps {}

interface HackerrankInterface{
  title: string,
  level: string,
  uri: string,
  tags: string
}


const redirectToLink=(url:string):any =>{
  const win = window.open("https://www.hackerrank.com/"+url, '_blank');
  if (win != null) {
    win.focus();
  }
  return win;
}
function loaderHackerrankData(): JSX.Element{
  return  <div className="loading-dim">
    <div className="loader0"></div>
    <div className="loader0"></div>
    <div className="loader0"></div>
  </div>  
}

function displayHackerrankData(hackerrankInterfaceData: HackerrankInterface[]): JSX.Element[]{
 let id=0;
 let image= "/assets/brand-logo/Hackerrank-Logo.png"
  return  hackerrankInterfaceData.map((hackerrankRepoList: any) => {
    hackerrankRepoList.id=id+=1;
    const styles: React.CSSProperties = {
      backgroundImage: `url(${image})`  ,
      backgroundSize: `contain` ,
      backgroundPositionX: `left` 
    }
    
    return (
      <div
        key={hackerrankRepoList.id}
        className="stackOverFlow-card background-image"
        style={styles}
        onClick={(event: React.MouseEvent<HTMLElement>) => {redirectToLink(hackerrankRepoList.uri)}}
      >
        <div className="github-card-content">
          <div className="stackOverFlow-card-content-items">
            <span className="stackOverFlow-card-title">
              {"stackQuesList.title".length > 100
                ? hackerrankRepoList.title.substring(0, 100) + "..."
                : hackerrankRepoList.title}
            </span>
            <span className="stackOverFlow-card-desc">
              {hackerrankRepoList.level}
            </span> 
            <span className="stackOverFlow-card-date">
              {hackerrankRepoList.tags}
            </span> 
          </div>
        </div>
      </div>
    );
  });
}


