import React, { lazy, Suspense } from 'react';

const LazyWorkedAndLearned = lazy(() => import('./WorkedAndLearned'));

const WorkedAndLearned = (props: JSX.IntrinsicAttributes & { children?: React.ReactNode; }) => (
  <Suspense fallback={null}>
    <LazyWorkedAndLearned {...props} />
  </Suspense>
);

export default WorkedAndLearned;
