import React, { lazy, Suspense } from 'react';

const LazyMyArticles = lazy(() => import('./MyArticles'));

const MyArticles = (props: JSX.IntrinsicAttributes & { children?: React.ReactNode; }) => (
  <Suspense fallback={null}>
    <LazyMyArticles {...props} />
  </Suspense>
);

export default MyArticles;
